import React from "react";
import ReactDOM from "react-dom";
import axios from "axios";

const mapStyles = {
  map: {
    position: "absolute",
    width: "100%",
    height: "100%",
  },
};
class CurrentLocation extends React.Component {
  constructor(props) {
    super(props);
    const { lat, lng } = this.props.initialCenter;
    this.state = {
      currentLocation: {
        lat: lat,
        lng: lng,
        address: "",
      },
    };
  }
  componentDidMount() {
    if (this.props.lat && this.props.lng) {
      // this.setState({
      //   currentLocation: {
      //     lat: coords.latitude,
      //     lng: coords.longitude
      //   }
      // });
    }
    if (this.props.centerAroundCurrentLocation) {
      if (navigator && navigator.geolocation) {
        navigator.geolocation.getCurrentPosition((pos) => {
          const coords = pos.coords;
          const myApiKey = "AIzaSyBtKD5VNiKZxh9cUkN0l0QmKp50OF5BGSI";
          axios
            .get(
              "https://maps.googleapis.com/maps/api/geocode/json?latlng=" +
                coords.latitude +
                "," +
                coords.longitude +
                "&key=" +
                myApiKey
            )
            .then((response) => {
              this.setState({
                currentLocation: {
                  lat: coords.latitude,
                  lng: coords.longitude,
                  address: response,
                },
              });
            });
        });
      }
    }
    this.loadMap();
  }

  getCurrentAddress = (value) => {
    const myApiKey = "AIzaSyBtKD5VNiKZxh9cUkN0l0QmKp50OF5BGSI";
    axios
      .get(
        "https://maps.googleapis.com/maps/api/geocode/json?latlng=" +
          value.latitude +
          "," +
          value.longitude +
          "&key=" +
          myApiKey
      )
      .then((response) => {});
  };

  componentDidUpdate(prevProps, prevState) {
    if (prevProps.google !== this.props.google) {
      this.loadMap();
    }
    if (prevState.currentLocation !== this.state.currentLocation) {
      this.recenterMap();
    }
  }

  loadMap() {
    if (this.props && this.props.google) {
      // checks if google is available
      const { google } = this.props;
      const maps = google.maps;
      const mapRef = this.refs.map;

      // reference to the actual DOM element
      const node = ReactDOM.findDOMNode(mapRef);
      let { zoom } = this.props;
      const { lat, lng } = this.state.currentLocation;
      const center = new maps.LatLng(lat, lng);

      const mapConfig = Object.assign({}, { center: center, zoom: zoom });

      // maps.Map() is constructor that instantiates the map
      this.map = new maps.Map(node, mapConfig);

      this.map.addListener("click", (e) => {
        const myApiKey = "AIzaSyBtKD5VNiKZxh9cUkN0l0QmKp50OF5BGSI";
        axios
          .get(
            "https://maps.googleapis.com/maps/api/geocode/json?latlng=" +
              e.latLng.lat() +
              "," +
              e.latLng.lng() +
              "&key=" +
              myApiKey
          )
          .then((response) => {
            this.setState({
              currentLocation: {
                lat: e.latLng.lat(),
                lng: e.latLng.lng(),
                address: response,
              },
            });
            this.props.getMapLocation(e.latLng.lat(), e.latLng.lng(), response);
          });
      });
    }
  }

  recenterMap() {
    const map = this.map;
    const current = this.state.currentLocation;
    const google = this.props.google;
    const maps = google.maps;

    if (map) {
      let center = new maps.LatLng(current.lat, current.lng);
      map.panTo(center);
    }

    // const marker = maps.Marker({
    //     position: new maps.LatLng(current.lat, current.lng),
    //     map,
    //     title: "Click to zoom",
    //   });

    //   marker.addListener("click", () => {
    //     map.setZoom(8);
    //     map.setCenter(marker.getPosition());
    //   });
  }

  onMapClicked(mapProps, map, clickEvent) {}

  renderChildren() {
    const { children } = this.props;
    if (!children) return;
    return React.Children.map(children, (c) => {
      if (!c) return;
      return React.cloneElement(c, {
        map: this.map,
        google: this.props.google,
        mapCenter: this.state.currentLocation,
      });
    });
  }

  render() {
    const style = Object.assign({}, mapStyles.map);
    return (
      <div>
        <div style={style} ref="map">
          Loading map...
        </div>
        {this.renderChildren()}
      </div>
    );
  }
}

export default CurrentLocation;

CurrentLocation.defaultProps = {
  zoom: 14,
  initialCenter: {
    lat: -1.2884,
    lng: 36.8233,
  },
  centerAroundCurrentLocation: false,
  visible: true,
};
