import React, { useState } from "react";
import { Layout, Row, Col, Button, Input, Breadcrumb } from "antd";
import { LoggedinHeader } from "../../containers";
import OrgSidebar from "../../containers/OrgSidebar";
import dotsicon from "../../assets/icons/dots-icon.svg";
import { NavLink } from "react-router-dom";
import ReactHtmlParser from "react-html-parser";
const { Content } = Layout;

const InfoDetails = (props) => {
  
  const info = props.location.state?props.location.state:{}
  return (
    <React.Fragment>
      <React.Fragment>
        {" "}
        <Row className="inner-header">
          <LoggedinHeader amORGUser />
        </Row>
        <Row className="content-center pt-0">
          <Layout>
            <Layout>
              <OrgSidebar amORGUser data={props} />
              <Content>
              <Row className="pt-3">
                  <Breadcrumb separator=">">
                    <NavLink to="/orgcc/infodirectory">
                      <Breadcrumb.Item>Info</Breadcrumb.Item>
                    </NavLink>
                    <Breadcrumb.Item>{info.title}</Breadcrumb.Item>
                  </Breadcrumb>
                </Row>
                <Row className="py-3 justify-content-between">
                  <h1 className="mb-0">
                    {" "}
                    {info.title}
                  </h1>
                </Row>

                <div className="mid-content">
                  <Row>
                    <Col span={22} className="editor-data">
                      <Row>
                        <Col span={24} className="mb-2">
                          {ReactHtmlParser(info.message)}
                        </Col>
                      </Row>
                    </Col>
                    <Col span={2} className="d-flex justify-content-end">
                      {/* <span className="ico-grid-dots">
                        <img src={dotsicon} alt="DotIcon" />
                      </span> */}
                    </Col>
                  </Row>
                </div>
              </Content>
            </Layout>
          </Layout>
        </Row>
      </React.Fragment>
    </React.Fragment>
  );
};

export default InfoDetails;
