import React, { useEffect, useState } from "react";
import Info from "./Info";
import { connect } from "react-redux";
import { getInfoAsync } from "../../actions/info";
import CreateInfo from "./CreateInfo";

const InfoContainer = (props) => {
  const [loader, setLoader] = useState(false);
  const [statusClicked, setStatusClicked] = useState(false);
  const [search,setSearch] =useState("")
  useEffect(() => {
    const { dispatch } = props;
    dispatch(getInfoAsync(search));
  }, [statusClicked,props.createinfodata,search]);

  useEffect(() => {
    if (props.infosdata === null) {
      setLoader(true);
    } else if (props.infosdata !== null) {
      setLoader(false);
    }
  }, [props.infosdata]);

  const setSearchValue =(value)=>{
    setSearch(value)
    console.log(search)
  }

  const statusOnClicked = () => {
    setStatusClicked(!statusClicked);
  };

  return !loader && props.infosdata !== null && props.infosdata.length === 0 && search.length === 0 ? (
  
    <CreateInfo data={props} />
  ) : (
    
    <Info
      infos={props.infosdata?props.infosdata:[]}
      data={props}
      loader={loader}
      statusOnClicked={statusOnClicked}
      setSearchValue={setSearchValue}
    />
 
  );
};

function mapStateToProps(state) {
  const { infosdata, createinfodata} = state.info;
  return {
    infosdata,
    createinfodata
  };
}

export default connect(mapStateToProps)(InfoContainer);
