import React, { useEffect, useState } from "react";
import { Layout, Row, Col, Form, Button, Input, Breadcrumb,Alert,Spin } from "antd";
import { LoggedinHeader } from "../../containers";
import OrgSidebar from "../../containers/OrgSidebar";
import FloatLabel from "../form/FloatLabels";
import { NavLink, useHistory } from "react-router-dom";
import { connect } from "react-redux";
import { createHelpAsync } from "../../actions/help";
import RichTextEditor from "../broadcastMessages/Editor";
import { REQUIRED_MESSAGE } from "../../errors";
const { Content } = Layout;

const CreateHelpForm = (props) => {
  const history = useHistory();
  const [help, sethelp] = useState({
    title: "",
    message: "",
  });
  const [title, setTitle] = useState("Create Help")
  const [error, showError] = useState(false);
  const [loader, setLoader] = useState(false);

  useEffect(() => {
    if (props.location.state && props.location.state.help) {
      sethelp(props.location.state.help)
      setTitle("Edit Help")
    }
  }, []);

  const onCreateChange = (e) => {
    sethelp({ ...help, [e.target.id]: e.target.value });
  };

  const onBroadMessageChange = (content, name) => {
    sethelp((prevState) => {
      return { ...prevState, [name]: content }
    })
  };

  const saveHelp = () => {
    setLoader(true)
    if (help.title &&  help.message ) {
      if (title === "Create Help") {
        showError(false);
        const { dispatch } = props;
        dispatch(createHelpAsync(help));
        setLoader(false)
        history.push("/orgcc/help")
      } else if (title === "Edit Help") {
        showError(false);
        const { dispatch } = props;
        dispatch(createHelpAsync(help));
        setLoader(false)
        history.push("/orgcc/help")
      }
        
    } else {
        showError(true);
    }
   
  }

  return (
    <React.Fragment>
      <React.Fragment>
        {" "}
        <Row className="inner-header">
          <LoggedinHeader amORGUser />
        </Row>
        <Row className="content-center pt-0">
          <Layout>
            <Layout>
              <OrgSidebar amORGUser data={props.data} />
              <Content>
                <Row className="pt-3">
                  <Breadcrumb separator=">">
                    <NavLink to="/orgcc/help">
                      <Breadcrumb.Item>Help</Breadcrumb.Item>
                    </NavLink>
                    <Breadcrumb.Item>{title}</Breadcrumb.Item>
                  </Breadcrumb>
                </Row>
                <Row className="py-3">
                  <h1 className="mb-0">{title}</h1>
                </Row>
                <div className="mid-content">
                <Spin spinning={loader} size="large">
                  <Row>
                    <Col span={24}>
                      <Form
                        className="input-md floating-label w-100"
                        name="validate_other"
                      >
                        {error?<Form.Item>
                          <Alert
                            message={REQUIRED_MESSAGE}
                            type="error"
                            showIcon
                          ></Alert>
                        </Form.Item>:""}
                        <Col span={24}>
                          <Row>
                            <Col span={24} md={18} className="gutter-row">
                              <FloatLabel label="Message Title" value={help.title}>
                                <Form.Item
                                  className="required"
                                  name="title"
                                  id="title"
                                  validateStatus="success"
                                >
                                  {console.log(help.title)}
                                  <Input
                                    className="input-control"
                                    id="title"
                                    name="title"
                                    htmlFor="helptext"
                                    autoComplete={"off"}
                                    value={help.title}
                                    onChange={(e) => onCreateChange(e)}
                                    maxLength={80}
                                  />
                                </Form.Item>
                              </FloatLabel>
                            </Col>
                          </Row>
                        </Col>
                        <Col span={24}>
                          <Row>
                            <Col span={24} md={18} className="gutter-row">
                              <FloatLabel label="Message" value={help.message}>
                                <Form.Item
                                  className="required"
                                  name="message"
                                  validateStatus="success"
                                >
                                  {console.log(help.message)}
                                  <RichTextEditor
                                    value={help.message}
                                    onChange={(content) => onBroadMessageChange(content, "message")}
                                    id="message"
                                  />
                                </Form.Item>
                              </FloatLabel>
                            </Col>
                          </Row>
                        </Col>

                        <Form.Item>
                          <div className="steps-action mt-5">
                            <Button onClick={saveHelp} type="primary" htmlType="submit">
                            Save
                            </Button>
                            <Button onClick={() => {
                              history.push("/orgcc/help");
                            }} className="ml-4" type="secondary">
                              Cancel
                            </Button>
                          </div>
                        </Form.Item>
                      </Form>
                    </Col>
                  </Row>
                  </Spin>
                </div>
              </Content>
            </Layout>
          </Layout>
        </Row>
      </React.Fragment>
    </React.Fragment>
  );
};

function mapStateToProps(state) {
  const { createhelpdata } = state.help;
  return {
    createhelpdata,
  };
}

export default connect(mapStateToProps)(CreateHelpForm);
