import React, { useEffect,useState } from "react";
import { Layout, Row, Col, Form, Button, Input, Breadcrumb,Alert } from "antd";
import { NavLink, useHistory } from "react-router-dom";
import { LoggedinHeader } from "../../containers";
import OrgSidebar from "../../containers/OrgSidebar";
import FloatLabel from "../form/FloatLabels";
import RichTextEditor from "../broadcastMessages/Editor";
import { createInfoAsync } from "../../actions/info";
import { connect } from "react-redux";
import { REQUIRED_MESSAGE } from "../../errors";
const { Content } = Layout;

const CreateInfoForm = (props) => {
  const history = useHistory();
  const [info, setinfo] = useState({
    title: "",
    message: "",
  });
  const [title, setTitle] = useState("Create Info")
  const [error, showError] = useState(false);

  useEffect(() => {
    if (props.location.state && props.location.state) {
      setinfo(props.location.state)
      setTitle("Edit Info")
    }
  }, []);

  const onCreateChange = (e) => {
    setinfo({ ...info, [e.target.id]: e.target.value });
  };

  const onBroadMessageChange = (content, name) => {
    setinfo((prevState) => {
      return { ...prevState, [name]: content }
    })
  };

  const saveInfo = () => {
    if (info.title && info.message) {
      if (title === "Create Info") {
        showError(false);
        const { dispatch } = props;
        dispatch(createInfoAsync(info));
        history.push("/orgcc/infodirectory")
      } else if (title === "Edit Info") {
        showError(false);
        const { dispatch } = props;
        dispatch(createInfoAsync(info));
        history.push("/orgcc/infodirectory")
      }

    } else {
      showError(true);
    }

  }

  return (
    <React.Fragment>
      <React.Fragment>
        {" "}
        <Row className="inner-header">
          <LoggedinHeader amORGUser />
        </Row>
        <Row className="content-center pt-0">
          <Layout>
            <Layout>
              <OrgSidebar amORGUser data={props} />
              <Content>
                <Row className="pt-3">
                  <Breadcrumb separator=">">
                    <NavLink to="/managebroadcast">
                      <Breadcrumb.Item>Info</Breadcrumb.Item>
                    </NavLink>
                    <Breadcrumb.Item>{title}</Breadcrumb.Item>
                  </Breadcrumb>
                </Row>
                <Row className="py-3">
                  <h1 className="mb-0">{title}</h1>
                </Row>
                <div className="mid-content">
                  <Row>
                    <Col span={24}>
                      <Form
                        className="input-md floating-label w-100"
                        name="validate_other"
                      >
                        {error?<Form.Item>
                          <Alert
                            message={REQUIRED_MESSAGE}
                            type="error"
                            showIcon
                          ></Alert>
                        </Form.Item>:""}
                        <Col span={24}>
                          <Row>
                            <Col span={24} md={18} className="gutter-row">
                              <FloatLabel label="Message Title" value={info.title}>
                                <Form.Item
                                  className="required"
                                  name="title"
                                  id="title"
                                  validateStatus="success"
                                >
                                  {console.log(info.title)}
                                  <Input
                                    className="input-control"
                                    id="title"
                                    name="title"
                                    htmlFor="helptext"
                                    autoComplete={"off"}
                                    value={info.title}
                                    onChange={(e) => onCreateChange(e)}
                                    maxLength={80}
                                  />
                                </Form.Item>
                              </FloatLabel>
                            </Col>
                          </Row>
                        </Col>
                        <Col span={24}>
                          <Row>
                            <Col span={24} md={18} className="gutter-row">
                              <FloatLabel label="Message" value={info.message}>
                                <Form.Item
                                  className="required"
                                  name="message"
                                  validateStatus="success"
                                >
                                  {console.log(info.message)}
                                  <RichTextEditor
                                    value={info.message}
                                    onChange={(content) => onBroadMessageChange(content, "message")}
                                    id="message"
                                  />
                                </Form.Item>
                              </FloatLabel>
                            </Col>
                          </Row>
                        </Col>

                        <Form.Item>
                          <div className="mt-5">
                            <Button onClick={saveInfo} type="primary" htmlType="submit">
                              SAVE
                            </Button>
                            <Button onClick={() => { history.push("/orgcc/infodirectory") }} className="ml-4" type="secondary">
                              Cancel
                            </Button>
                          </div>
                        </Form.Item>
                      </Form>
                    </Col>
                  </Row>
                </div>
              </Content>
            </Layout>
          </Layout>
        </Row>
      </React.Fragment>
    </React.Fragment>
  );
};


function mapStateToProps(state) {
  const { createinfodata } = state.info;
  return {
    createinfodata,
  };
}

export default connect(mapStateToProps)(CreateInfoForm);
