import React, { Component } from "react";
import { Modal, Button, Row } from "antd";
import "../modal/modal.scss";
import MapContainer from "./GMap";
import {
  ADD_LOCATION,
  LOCATION,
  ADD_TEXT_MODAL,
  CANCEL_MODAL,
  SELECT_LOCATION,
  CLOSE_TEXT_MODAL,
} from "../../headings";

class Location extends Component {
  state = {
    visible: this.props.viaModal ? true : false,
    latitude: "",
    longitude: "",
    location: "",
  };

  showModal = () => {
    this.setState({
      visible: true,
      latitude: this.props.viaModal ? this.props.latitude : "",
      longitude: this.props.viaModal ? this.props.longitude : "",
      locationName: "",
    });

    if (this.props.isEdit) {
      this.setState({
        latitude: this.props.latitude,
        longitude: this.props.longitude,
        location: this.props.address,
      });
    }
  };

  handleOk = () => {
    this.setState({
      visible: false,
    });
    if (this.props.viaModal) {
      this.props.setShowMap(false);
    } else {
      this.props.setLocation(
        this.state.longitude,
        this.state.latitude,
        this.state.locationName
      );
    }
  };

  getMapLocation = (lag, lat, name) => {
    this.setState({
      latitude: lat,
      longitude: lag,
      locationName: name.data.results[0].formatted_address,
    });
  };

  getCurrenLocationName = (locationname) => {
    this.setState({ locationName: locationname });
  };

  handleCancel = () => {
    this.setState({
      visible: false,
    });
    if (this.props.viaModal) {
      this.props.setShowMap(false);
    }
  };

  render() {
    return (
      <>
        {!this.props.viaModal ? (
          <div class="w-25 w-md-100">
            {" "}
            <Button
              type="link"
              className="upload-list link pl-0"
              onClick={this.showModal}
              disabled={
                this.props.documents.length > 0 || this.props.images.length > 0
              }
            >
              <span className="icon icon-location"></span>
              {this.props.viaMessages ? ADD_LOCATION : LOCATION}
            </Button>
            <div className="text-truncate w-75"> {this.props.locationName}</div>
          </div>
        ) : null}
        <Modal
          title={this.props.viaModal ? "" : SELECT_LOCATION}
          visible={this.state.visible}
          onOk={this.handleOk}
          onCancel={this.handleCancel}
          centered
          width={800}
          okButtonProps={{
            children: "Custom OK",
          }}
          cancelButtonProps={
            this.props.viaModal
              ? {
                  style: { display: "none" },
                }
              : {
                  children: "Custom cancel",
                  className: "ant-btn-secondary",
                }
          }
          okText={this.props.viaModal ? CLOSE_TEXT_MODAL : ADD_TEXT_MODAL}
          cancelText={CANCEL_MODAL}
        >
          <Row className="py-4 map-container">
            <MapContainer
              getMapLocation={this.getMapLocation}
              getCurrenLocationName={this.getCurrenLocationName}
              longitude={this.props.longitude}
              latitude={this.props.latitude}
            />
          </Row>
        </Modal>
      </>
    );
  }
}

export default Location;
